<!--服务质量过程监控-停电信息-->
<template>
  <div class="outage">
    <!-- 停电信息类型占比 -->
    <div
      class="outage_left charts_main"
      v-loading="!resTdxxCharts"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">停电信息类型占比</div>
      <div class="pie_charts">
        <bda-pie-charts
          class="pieCharts"
          v-if="resTdxxCharts"
          :pieChartsData="tdxxChartsData"
          :roseType="false"
          :radius="['50%', '75%']"
          :center="['29%', 'center']"
          :labelType="'emphasis'"
          :legendShow="true"
        ></bda-pie-charts>
      </div>
    </div>

    <!-- 停电状态占比 -->
    <div
      class="outage_center charts_main"
      v-loading="!resTdztCharts"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">停电状态占比</div>

      <div class="pie_charts">
        <bda-pie-charts
          class="pieCharts"
          v-if="resTdztCharts"
          :pieChartsData="tdztChartsData"
          :roseType="false"
          :radius="['50%', '75%']"
          :center="['35%', 'center']"
          :labelType="'emphasis'"
          :legendShow="true"
        ></bda-pie-charts>
      </div>
    </div>
    <div
      class="outage_right charts_main"
      v-loading="!outageLstatus"
      v-if="outageLstatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">供电单位停电信息公告条次</div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="outageLstatus"
          :chartsConfig="outageLchartsConfig"
          :barType="chartsType"
          :barWidth="6"
        ></bda-3d-barCharts>
      </div>
    </div>
    <div
      class="outage_right charts_main"
      v-loading="!outageRstatus"
      v-if="outageRstatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">供电单位停电信息公告条次</div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="outageRstatus"
          :chartsConfig="outageRchartsConfig"
          :barType="chartsType"
          :barWidth="4"
        ></bda-3d-barCharts>
      </div>
    </div>
    <!-- 供电单位计划及临时停电公告条次 -->
    <!-- <div class="power_outage">
            <div class="outage_maim">
                <div class="power_outage_left" v-loading="!outageLstatus"
                     element-loading-background="rgba(0, 0, 0, 0.2)"
                     element-loading-text="加载中">
            <div class="title">
                供电单位停电信息公告条次
            </div>
                    <bda-3d-barCharts v-if="outageLstatus" :chartsConfig='outageLchartsConfig' :barType='chartsType'
                                      :barWidth="2">
                    </bda-3d-barCharts>
                    <div class="noData" v-if="outageLchartsConfig.dimension && outageLchartsConfig.dimension.length===0">
                        <span>暂无相关数据信息统计！</span>
                    </div>
                </div>

                <div class="power_outage_right" v-loading="!outageRstatus"
                     element-loading-background="rgba(0, 0, 0, 0.2)"
                     element-loading-text="加载中">
                    <bda-3d-barCharts v-if="outageRstatus" :chartsConfig='outageRchartsConfig' :barType='chartsType'
                                      :barWidth="2">
                    </bda-3d-barCharts>
                    <div class="noData"  v-if="outageRchartsConfig.dimension && outageRchartsConfig.dimension.length===0">
                        <span>暂无相关数据信息统计！</span>
                    </div>
                </div>
            </div>
    </div>-->
  </div>
</template>

<script>
import bdaPieCharts from '../../../widgets/bda-pie-charts/bda-pie-charts'
import bda3dBarCharts from '@/widgets/bda-3d-barCharts/bda-3d-barCharts'
import {
  dataChange,
  dataFilter,
  translateFilter
} from '../../../commonJS/dataChanges'
import config from '../../../base/mixin/service-quality-process';

export default {
  name: "outage",
  mixins: [config],
  components: {
    bdaPieCharts,
    bda3dBarCharts
  },
  data() {
    return {
      // 饼图数据(停电信息)是否返回
      resTdxxCharts: false,
      // 饼图数据(停电状态)是否返回
      resTdztCharts: false,
      //  // 柱状图数据(停电公告左)是否返回
      //  resCswcCharts: false,
      //  // 柱状图数据(停电公告右)是否返回
      //  resCswcCharts: false,
      // 饼图数据(停电信息)
      tdxxChartsData: {},
      // 饼图数据(停电状态)
      tdztChartsData: {},
      //  // 柱状图数据(停电公告左)
      //  tdggLChartsConfig: [],
      //  tdggLbarType: 'stack',
      //  // 柱状图数据(停电公告右)
      //  tdggRChartsConfig: [],
      //  tdggRbarType: 'stack',
      chartsType: 'stack',
      outageLchartsConfig: {},
      outageRchartsConfig: {},
      outageLstatus: false,
      outageRstatus: false
    }
  },
  watch: {},
  mounted() {
  },
  created() {
    this.getData();
  },
  methods: {
    getPieData() {
      //停电信息
      this.getPowerCutForType({
        orgType: this.$parent.orgType,
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      }).then(res => {
        this.tdxxFilter(res)
      }).catch(() => {
        this.resTdxxCharts = true;
      })
      //  停电状态
      this.getPowerCutForStatus({
        orgType: this.$parent.orgType,
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      }).then(res => {
        this.tdztFilter(res)
      }).catch(() => {
        this.resTdztCharts = true;
      })
    },
    //停电信息
    tdxxFilter(data) {
      let pieData = {
        data: [],
        color: []
      }
      pieData.color = ['#2CFAFF', '#00CDFF', '#00ABFF', '#0091FF', '#0079FF', '#005FFF', '#4D7EFF', '#8655ff']

      if (data && data.length !== 0) {
        pieData.dataMap = data.map(e => {
          return {
            value: e.ccsum,
            name: e.name
          }
        })
      }
      this.tdxxChartsData = pieData;
      this.resTdxxCharts = false;
      this.$nextTick(() => {
        this.resTdxxCharts = true;
      })
    },
    //停电状态
    tdztFilter(data) {
      let pieData = {
        data: [],
        color: []
      }
      pieData.color = ['#39CFA8', '#67E568', '#9EFF72', '#F5FF66 ']

      if (data && data.length !== 0) {
        pieData.dataMap = data.filter(item => item.status !== '04').map(e => {
          return {
            value: e.ccsum,
            name: e.status === '02' ? e.name = '部分送电' : e.name
          }
        })
      }
      this.resTdztCharts = false;
      this.tdztChartsData = pieData
      this.$nextTick(() => {
        this.resTdztCharts = true;
      })
    },
    // 停电公告信息
    getTdData() {
      let $this = this;
      let type = this.$parent.orgType

      this.getPowerCutForTemporary({
        orgType: type,
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      }).then(res => {
        if (res && res.length !== 0) {
          let data = translateFilter(res, $this.$parent.orgTranslate, 'orgno', 'orgName');
          let _info = dataChange(data, 'orgno', 'orgName', true);
          let nData = dataFilter(_info, {
            name: 'status',
            translate: {
              '01': '全部送电',
              '02': '部分送电',
              '03': '未送电',
            },
            valueField: 'ccsum'
          }, 'orgName');
          let colorMap = {}
          colorMap['全部送电'] = ['#005FFF', '#009DFF']
          colorMap['部分送电'] = ['#00ABFF', '#00E5FF']
          colorMap['未送电'] = ['#6AFFA2', '#00BDDE']
          let dataDeal = {}
          let quotaMap = {}
          quotaMap['全部送电'] = nData.quotaMap['全部送电']
          quotaMap['部分送电'] = nData.quotaMap['部分送电']
          quotaMap['未送电'] = nData.quotaMap['未送电']
          dataDeal = {
            colorMap: colorMap,
            dimension: nData.dimension,
            quotaMap: quotaMap
          }
          if (type === '1') {
            $this.outageLchartsConfig = dataDeal;
            $this.outageRstatus = false;
            $this.$nextTick(() => {
              $this.outageLstatus = true;
            })
          } else {
            $this.outageRchartsConfig = dataDeal;
            $this.outageLstatus = false;
            $this.$nextTick(() => {
              $this.outageRstatus = true;
            })
          }

        } else {
          if (type === '1') {
            $this.outageLstatus = true;
          } else {
            $this.outageRstatus = true;
          }
        }
      })
        .catch(() => {
          if (type === '1') {
            $this.outageLstatus = true;
          } else {
            $this.outageRstatus = true;
          }
        })

    },
    getData() {
      this.getTdData();
      this.getPieData();
    }
  }

}
</script>

<style lang="scss" scoped>
.outage {
  display: flex;
  //  justify-content: space-around;
  //  margin-top: -10px;
  height: 100%;
  width: 100%;
  .noData {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: #fff;
      font-size: 14px;
    }
  }

  .title {
    width: 100%;
    height: 16%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 20px;
    color: #00e7ff;
  }

  .outage-left {
    width: 35%;
    float: left;
    height: 100%;
    position: relative;
    margin-top: 15px;
  }

  .charts_main {
    padding: 0 20px;
    box-sizing: border-box;
  }

  .outage_left {
    width: calc(22%);
    background: url("../../../assets/img/监控框4.png") no-repeat top center;
    background-size: 100% 100%;
    height: 100%;
    float: left;
    position: relative;
    .pie_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
  .outage_center {
    width: calc(22%);
    background: url("../../../assets/img/监控框4.png") no-repeat top center;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .pie_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
  .outage_right {
    width: 56%;
    float: left;
    height: 100%;
    position: relative;
    background: url("../../../assets/img/监控框7.png") no-repeat top center;
    background-size: 100% 100%;
    .bar_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
}
</style>