<template>
  <div class="power">
    <div
      class="power_left charts_main"
      v-loading="!resPieDataStatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">供电质量类型占比</div>
      <div class="pie_charts">
        <bda-pie-charts
          class="pieCharts"
          v-if="resPieDataStatus"
          :pieChartsData="pieData"
          :roseType="false"
          :radius="['45%', '75%']"
          :center="['30%', 'center']"
          :labelType="'emphasis'"
          :legendShow="true"
        ></bda-pie-charts>
      </div>
    </div>

    <div
      class="power_center"
      v-loading="!zbRes"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div v-for="(v, k, i) in zbMap" :key="i" class="count_item">
        <div class="count_title">{{ k }}</div>
        <div class="count_value">
          <span>属实量</span>
          <span>总量</span>
          <span>{{ parseInt(v) }}</span>
          <span>{{ parseInt(zbCount[k]) }}</span>
        </div>
      </div>
    </div>
    <div
      class="power_right charts_main"
      v-loading="!dwChartsStatus"
      v-if="dwChartsStatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <el-carousel :autoplay="false" :arrow="'always'">
        <el-carousel-item>
          <div class="title">电压质量长时间异常属实量/总量</div>
          <div class="bar_charts_scroll">
            <bda-bar-charts
              v-if="dwChartsStatus"
              :chartsConfig="dwChartsData"
              :barType="barType"
              :bar-width="6"
            ></bda-bar-charts>
          </div>
        </el-carousel-item>
        <el-carousel-item>
          <div class="title">供电频率长时间异常总量/属实量</div>
          <div class="bar_charts_scroll">
            <bda-bar-charts
              v-if="dwChartsStatus && ycChartsStatus"
              :chartsConfig="ycChartsData"
              :barType="barType"
              :barWidth="4"
            ></bda-bar-charts>
          </div>
        </el-carousel-item>
        <el-carousel-item>
          <div class="title">频繁停电总量/属实量</div>
          <div class="bar_charts_scroll">
            <bda-bar-charts
              v-if="dwChartsStatus && tdChartsStatus"
              :chartsConfig="tdChartsData"
              :barType="barType"
              :barWidth="4"
            ></bda-bar-charts>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div
      class="power_right charts_main"
      v-loading="!gsChartsStatus"
      v-if="gsChartsStatus"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <el-carousel :autoplay="false" :arrow="'always'">
        <el-carousel-item>
          <div class="title">电压质量长时间异常属实量/总量</div>
          <div class="bar_charts_scroll">
            <bda-bar-charts
              v-if="gsChartsStatus"
              :chartsConfig="gsChartsData"
              :barType="barType"
              :bar-width="4"
            ></bda-bar-charts>
          </div>
        </el-carousel-item>
        <el-carousel-item>
          <div class="title">供电频率长时间异常总量/属实量</div>
          <div class="bar_charts_scroll">
            <bda-bar-charts
              v-if="gsChartsStatus && ycChartsStatus"
              :chartsConfig="ycChartsData"
              :barType="barType"
              :barWidth="4"
            ></bda-bar-charts>
          </div>
        </el-carousel-item>
        <el-carousel-item>
          <div class="title">频繁停电总量/属实量</div>
          <div class="bar_charts_scroll">
            <bda-bar-charts
              v-if="gsChartsStatus && tdChartsStatus"
              :chartsConfig="tdChartsData"
              :barType="barType"
              :barWidth="4"
            ></bda-bar-charts>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-quality-process';
import qs from 'qs';
import { dataChange, dataFilter, translateFilter } from '../../../commonJS/dataChanges'
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';
import bdaPieCharts from '../../../widgets/bda-pie-charts/bda-pie-charts'

export default {
  name: "power",
  mixins: [Interface],
  components: { bdaBarCharts, bdaPieCharts },
  data() {
    return {
      dwChartsStatus: false,
      dwChartsData: {},
      barType: 'stack',
      gsChartsData: {},
      ycChartsData: {},
      tdChartsData: {},
      gsChartsStatus: false,
      ycChartsStatus: false,
      tdChartsStatus: false,
      pieData: {
        dataMap: [],
        color: ['#2CFAFF', '#00CDFF', '#00ABFF', '#0091FF', '#0079FF', '#005FFF', '#4D7EFF', '#8655ff']
      },
      resPieDataStatus: false,
      zbCount: {
        '电压质量长时间异常': 0,
        '供电频率长时间异常': 0,
        '频繁停电': 0
      },
      zbMap: {
        '电压质量长时间异常': 0,
        '供电频率长时间异常': 0,
        '频繁停电': 0
      },
      zbRes: false
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.getData();
    this.getZbData();
  },
  methods: {
    getGdData() {
      let $this = this;
      let type = this.$parent.orgType
      this.qualityForVerified(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: type
      })).then(res => {
        if (res && res.length !== 0) {
          let data = translateFilter(res, $this.$parent.orgTranslate, 'orgno', 'orgName');
          let _info = dataChange(data, 'orgno', 'orgName');
          let colorMap = {
            '属实': ['#6AFFA2', '#00BDDE'],
            '不属实': ['#005FFF', '#009DFF'],
          }
          let nData = dataFilter(_info, {
            name: 'istrue',
            translate: {
              '1': '属实',
              '0': '不属实',
            },
            valueField: 'sum'
          }, 'orgName');
          nData.colorMap = colorMap;
          window.console.log(colorMap, 'nData')
          if (type === '1') {
            $this.dwChartsData = nData;
            $this.gsChartsStatus = false;
            $this.$nextTick(() => {
              $this.dwChartsStatus = true;
              this.getGdplData()
              this.getPftdData()
            })
          } else {
            $this.gsChartsData = nData;
            $this.dwChartsStatus = false;
            $this.$nextTick(() => {
              $this.gsChartsStatus = true;
              this.getGdplData()
              this.getPftdData()
            })
          }
        } else {
          // if (type === '1') {
          //   $this.dwChartsStatus = true;
          // } else {
          //   $this.gsChartsStatus = true;
          // }
        }
      })

        .catch(() => {
          if (type === '1') {
            $this.dwChartsStatus = true;
          } else {
            $this.gsChartsStatus = true;
          }
        })
    },
    // 供电频率长时间异常总量/属实量
    getGdplData() {
      let $this = this;
      let type = this.$parent.orgType
      this.getQualityLtAnomaly(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: type
      })).then(res => {
        let data = translateFilter(res, $this.$parent.orgTranslate, 'orgno', 'orgName');
        let _info = dataChange(data, 'orgno', 'orgName');
        let colorMap = {
          '属实': ['#6AFFA2', '#00BDDE'],
          '不属实': ['#005FFF', '#009DFF'],
        }
        let nData = dataFilter(_info, {
          name: 'istrue',
          translate: {
            '0': '不属实',
            '1': '属实'
          },
          valueField: 'sum'
        }, 'orgName');
        nData.colorMap = colorMap;
        $this.ycChartsData = nData;
        $this.ycChartsStatus = false;

        $this.$nextTick(() => {
          $this.ycChartsStatus = true;
        })
      })
    },
    // 频繁停电总量/属实量
    getPftdData() {
      let $this = this;
      let type = this.$parent.orgType
      this.getQualityFreqbko(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: type
      })).then(res => {
        let data = translateFilter(res, $this.$parent.orgTranslate, 'orgno', 'orgName');
        let _info = dataChange(data, 'orgno', 'orgName');
        let colorMap = {
          '属实': ['#6AFFA2', '#00BDDE'],
          '不属实': ['#005FFF', '#009DFF'],
        }
        let nData = dataFilter(_info, {
          name: 'istrue',
          translate: {
            '0': '不属实',
            '1': '属实'
          },
          valueField: 'sum'
        }, 'orgName');
        nData.colorMap = colorMap;
        $this.tdChartsData = nData;
        $this.tdChartsStatus = false;
        $this.$nextTick(() => {
          $this.tdChartsStatus = true;
        })

      })
    },
    getData() {
      this.getGdData();
      this.getPieData();
    },
    getPieData() {
      let $this = this;
      let dataFilter = (data, type) => {
        let infoData = [];
        if (data) {
          for (let item of data) {
            let _obj = {}
            if (type === '1') {
              _obj = {
                name: item.name,
                value: item.ccsum
              }
            }
            infoData.push(_obj)
          }
        }
        return infoData;
      };
      this.getQualityForType(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: this.$parent.orgType
      })).then(res => {
        let _dataInfo = dataFilter(res, '1');
        $this.pieData.dataMap = _dataInfo;
        window.console.log('_info', _dataInfo);
        $this.resPieDataStatus = false;
        $this.$nextTick(() => {
          $this.resPieDataStatus = true;
        })
      }).catch(() => {
        $this.resPieDataStatus = true;
      });

    },
    getZbData() {
      let $this = this;
      this.getQualityTarget(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      })).then((res) => {
        this.zbRes = true;
        //   window.console.log('res', res)
        if (res && res.length !== 0) {
          let _info = dataChange(res, 'name');
          //   window.console.log('_info', _info);
          if (_info) {
            for (let item of _info) {
              if (item.data) {
                for (let sys of item.data) {
                  $this.zbCount[item.name] += sys.ccsum;
                  if (sys.istrue === '1') {
                    $this.zbMap[item.name] = sys.ccsum;
                  }
                }
              }
            }
          }
        }
      }).catch(() => {
        this.zbRes = true;
      })
    }
  }
}
</script>

<style scoped lang="scss">
.power {
  display: flex;
  width: 100%;
  height: 100%;

  .noData {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: #fff;
      font-size: 14px;
    }
  }
  .title {
    width: 100%;
    height: 16%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 20px;
    color: #00e7ff;
  }
  .charts_main {
    padding: 0 20px;
    box-sizing: border-box;
  }

  .power_left {
    width: calc(22%);
    background: url("../../../assets/img/监控框4.png") no-repeat top center;
    background-size: 100% 100%;
    height: 100%;
    float: left;
    position: relative;
    .pie_charts {
      width: 100%;
      height: calc(100% - 16%);
    }
  }
  .power_center {
    width: calc(22%);
    // background: url("../../../assets/img/监控框4.png") no-repeat top center;
    // background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 16px;

    .count_item {
      width: 100%;
      height: 33%;
      background: url("../../../assets/img/监控框6-2.png") no-repeat top center;
      background-size: 100% 100%;
      box-sizing: border-box;
      padding: 10px 5px 10px 10px;
      .count_title {
        height: 38%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #00e7ff;
      }
      .count_value {
        height: calc(100% - 38%);

        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        color: #fff;
        span {
          width: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          &:nth-child(1) {
            color: #00ffb2;
          }
          &:nth-child(2) {
            color: #00e7ff;
          }
        }
        //     margin-top: 3px;
      }
    }
  }

  .power_right {
    width: 56%;
    float: left;
    height: 100%;
    position: relative;
    background: url("../../../assets/img/监控框7.png") no-repeat top center;
    background-size: 100% 100%;
    ::v-deep .el-carousel__arrow {
      background-color: rgb(31, 45, 61);
    }
    ::v-deep .el-carousel__arrow--left {
      left: 12px;
    }
    ::v-deep .el-carousel__arrow--right {
      right: 12px;
    }
    ::v-deep .el-carousel__indicators--horizontal {
      display: none;
    }
    .bar_charts {
      width: 100%;
      padding-top: 2%;
      height: calc(100% - 18%);
    }

    .bar_charts_scroll {
      width: calc(100% - 70px);
      height: calc(100% - 16%);
      padding: 0 50px;
    }
  }
}
</style>