<!--服务质量过程监控--->
<template>
  <div class="serviceQualityProcess">
    <div class="wd-style">
      <div class="date-data">
        <el-select v-model="dateType" style="width: 65px">
          <el-option
            v-for="item in dateTypeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="pic-data">
        <el-date-picker
          v-model="dateValue"
          align="right"
          type="date"
          placeholder="选择日期"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
        ></el-date-picker>
      </div>
    </div>
    <div class="left-content" v-if="loadAllMainRes">
      <total></total>
      <!-- <traffic :lineShow="lineShow" ref="traffic"></traffic> -->
    </div>
    <div class="right-content" v-if="loadAllMainRes" :class="bgImgChange()">
      <div class="tab-type">
        <div
          class="tab-title"
          @click="typeQh('1')"
          :class="orgType === '1' ? 'active' : ''"
        >
          主城渝西
        </div>
        <div
          class="tab-title"
          @click="typeQh('0')"
          :class="orgType === '0' ? 'active' : ''"
        >
          渝东南北
        </div>
      </div>
      <div class="right-content-title">
        <div class="content">
          <span
            :class="active === 0 ? 'active' : ''"
            @click="changeChildComp(0)"
            >汇总</span
          >
          <span
            :class="active === 1 ? 'active' : ''"
            @click="changeChildComp(1)"
            >故障报修</span
          >
          <span
            :class="active === 2 ? 'active' : ''"
            @click="changeChildComp(2)"
            >停电信息</span
          >
          <span
            :class="active === 3 ? 'active' : ''"
            @click="changeChildComp(3)"
            >投诉</span
          >
          <span
            :class="active === 4 ? 'active' : ''"
            @click="changeChildComp(4)"
            >供电质量</span
          >
        </div>
      </div>

      <div class="right-content-content">
        <div class="right-top-content">
          <traffic v-if="loadAllMainRes" :type="orgType"></traffic>
        </div>
        <div class="right-bottom-content">
          <div class="tabs-details">
            <gather v-if="active === 0 && paramsRes"></gather>
            <fault v-if="active === 1" ref="fault"></fault>
            <outage v-if="active === 2" ref="outage"></outage>
            <complaint v-if="active === 3" ref="complaint"></complaint>
            <power v-if="active === 4" ref="power"></power>
          </div>
        </div>
      </div>
    </div>

    <!-- <div class="detail_chart" v-if="loadAllMainRes">

      <div class="tab-menus-style-left">
        <div class>
          <div
            class="fault_info detail_tab"
            :class="{'active':active===1}"
            @click="changeChildComp(1)"
          >故障报修</div>
          <div
            class="outage_info detail_tab"
            :class="{'active':active===2}"
            @click="changeChildComp(2)"
          >停电信息</div>
        </div>
      </div>
      <div class="tab-menus-style-right">
        <div class>
          <div
            class="complaint_info detail_tab"
            :class="{'active':active===3}"
            @click="changeChildComp(3)"
          >投诉</div>
          <div
            class="power_info detail_tab"
            :class="{'active':active===4}"
            @click="changeChildComp(4)"
          >供电质量</div>
        </div>
      </div>
    </div>-->
    <!-- <div class="detail_tab-main">
           <div class="fault_info detail_tab " :class="{'active':active===1}" @click="changeChildComp(1)">故障报修</div>
           <div class="outage_info detail_tab " :class="{'active':active===2}" @click="changeChildComp(2)"> 停电信息</div>
           <div class="complaint_info detail_tab " :class="{'active':active===3}" @click="changeChildComp(3)">投诉</div>
           <div class="power_info detail_tab " :class="{'active':active===4}" @click="changeChildComp(4)">供电质量</div>
    </div>-->
  </div>
</template>

<script>
import traffic from "./traffic/traffic"
import gather from "./gather/gather"
import fault from "./fault/fault"
import outage from "./outage/outage"
import complaint from "./complaint/complaint"
import power from "./power/power"
import total from "./total/total"
import Interface from '../../base/mixin/service-quality-process';

export default {
  name: "serviceQualityProcess",
  components: {
    traffic,
    gather,
    fault,
    outage,
    complaint,
    power,
    total
  },
  mixins: [Interface],
  data() {
    return {
      //控制当前显示页面
      active: 0,
      //显示边框线
      lineShow: true,
      dateType: '3',   // 当前类型
      dateTypeOptions: [
        { value: '1', label: '年' },
        { value: '2', label: '月' },
        { value: '3', label: '日' }
      ],
      dateParams: '', // 日期数据
      paramsRes: true, // 数据刷新
      dateValue: '',
      orgTranslate: {},    // 组织信息
      orgType: '1',
      loadAllMainRes: false
    }
  },
  watch: {
    dateType() {
      //  this.dateType='2';
      this.orgType = '1';
      this.loadAllMainRes = false;
      this.$nextTick(() => {
        this.loadAllMainRes = true
      })
    },
    orgType() {
      if (this.active === 0) {
        this.paramsRes = false;
        this.$nextTick(() => {
          this.paramsRes = true
        })
      } else if (this.active === 1) {
        this.$refs.fault.getData();
      } else if (this.active === 2) {
        this.$refs.outage.getData();
      } else if (this.active === 3) {
        this.$refs.complaint.getData();
      }
      else if (this.active === 4) {
        this.$refs.power.getData();
      }

    },
    // active(v) {
    // if (v !== 0) {
    //   this.$refs.traffic.zwBorderRes = false
    // } else {
    //   this.$refs.traffic.zwBorderRes = true
    // }
    // },
    dateValue(v) {
      let str = v.replace(/-/g, '');
      this.dateParams = str;
      this.orgType = '1';
      this.loadAllMainRes = false;
      this.$nextTick(() => {
        this.loadAllMainRes = true
      })
    }
  },
  created() {
    this.getOrgTranslate({}).then(res => {
      this.orgTranslate = res;
      this.$nextTick(() => {
        this.loadAllMainRes = true;
      })
    });
    this.dateValue = this.$parent.date;
    let str = this.dateValue.replace(/-/g, '');
    this.dateParams = str;
  },
  mounted() {

  },
  methods: {
    bgImgChange() {
      let className = ''

      if (this.orgType === '1') {
        //主城渝西
        className = 'active' + (this.active + 1)
      }
      if (this.orgType === '0') {
        //渝东南北
        className = 'active' + (this.active + 6)
      }
      return className
    },
    changeChildComp(n) {
      this.active = n
      if (this.active !== 0) {
        this.lineShow = false
      }
    },
    changeDataType(type) {
      this.dateType = type;
    },
    // 供电类型切换
    typeQh(type) {
      this.orgType = type;
    }
  }
}
</script>

<style lang="scss" scoped>
.serviceQualityProcess {
  .wd-style {
    position: absolute;
