    padding: 0 20px 0 30px;
    margin-top: 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;

    .line-style {
      border-top: 1px solid #b7f4ff;
      height: 10px;
      width: 52px;
      float: left;
      margin-top: 15px;
    }

    .label-icon {
