      /deep/ .el-input__icon {
        font-size: 16px;
        color: #00e7ff;
      }
      // }
    }
  }
  width: calc(100% - 15px);
  height: 100%;
  position: relative;
  box-sizing: border-box;
  padding: 0 70px;
  display: flex;
  .left-content {
    width: 24%;
    height: 100%;
  }
  .right-content {
    width: 76%;
    height: calc(100% - 20px);
    position: relative;
    background-size: 100% 100%;
    .right-content-title {
      padding-left: 108px;
      width: 100%;
      height: 10%;
      display: flex;
      justify-content: center;
      .content {
        width: 43%;
        display: flex;
        justify-content: center;
        align-items: center;
        span {
          flex: 1;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #6af;
          font-size: 16px;
        }
      }
    }
    .right-content-content {
      width: 100%;
      height: 90%;

      .right-top-content {
        width: calc(100% - 90px);
        height: 48%;
        padding: 0px 65px;
      }
      .right-bottom-content {
        // margin-top: 20px;
        width: calc(100% - 96px);
        height: 48%;
        padding: 0px 70px;

        .tabs-details {
          width: 100%;
          height: 100%;
        }
      }
    }
    .tab-type {
      // z-index: 10;
      position: absolute;
      width: 30px;
      left: 20px;
      top: 20%;
      text-align: center;
      height: 60%;
      line-height: 22px;
      display: flex;
      flex-direction: column;
      justify-items: center;
      align-items: center;
      color: #6af;
      .tab-title {
        height: 50%;
        font-size: 22px;
        display: flex;
        justify-items: center;
        align-items: center;
        /*cursor: pointer;*/
        /*writing-mode: tb-rl;*/
      }
      .tab-title.active {
        color: #fff !important;
      }
    }
  }
  span.active {
    color: #fff !important;
  }
  .right-content.active1 {
    background: url("../../assets/img/监控框3-1.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .right-content.active2 {
    background: url("../../assets/img/监控框3-2.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .right-content.active3 {
    background: url("../../assets/img/监控框3-3.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .right-content.active4 {
    background: url("../../assets/img/监控框3-4.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .right-content.active5 {
    background: url("../../assets/img/监控框3-5.png") no-repeat top center;
    background-size: 100% 100%;
  }

  .right-content.active6 {
    background: url("../../assets/img/监控框3-6.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .right-content.active7 {
    background: url("../../assets/img/监控框3-7.png") no-repeat top center;
