  }
  .right-content.active8 {
    background: url("../../assets/img/监控框3-8.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .right-content.active9 {
    background: url("../../assets/img/监控框3-9.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .right-content.active10 {
    background: url("../../assets/img/监控框3-10.png") no-repeat top center;
    background-size: 100% 100%;
  }
}
</style>