<!-- 服务质量过程监控-业务量 -->
<template>
  <div class="traffic">
    <div class="plant_traffic">
      <div
        class="plant_traffic-main"
        v-loading="!barChartsRes"
        element-loading-background="rgba(0, 0, 0, 0.6)"
        element-loading-text="加载中"
      >
        <div class="plant_traffic-right">
          <div class="plant_traffic-right-main">
            <div class="details-style" style="margin-top: 7px">
              <div class="title-info">{{ gdTitle }}业务量</div>
              <!--                            <div class="count-info">{{gdCount.toLocaleString()}}</div>-->
              <div class="count-info">{{ gdCount }}件</div>
            </div>
            <div
              class="details-style"
              v-for="(v, key, i) in rightDataMap"
              :key="i"
            >
              <div class="title-info">{{ key }}</div>
              <div class="count-info">{{ parseInt(v) }}件</div>
            </div>
          </div>
        </div>
        <div class="plant_traffic-left" id="plant_traffic_chart">
          <bda-bar-charts
            v-if="barChartsRes"
            :chartsConfig="barCharts"
            :barType="barType"
            :barXData="barChartsX"
            :barWidth="6"
          ></bda-bar-charts>
          <div
            class="noData"
            v-if="barCharts.dimension && barCharts.dimension.length === 0"
          >
            <span>暂无相关数据信息统计！</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from '../../../base/mixin/service-quality-process';
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts'
import qs from 'qs'
import { dataChange, dataFilter, translateFilter } from '../../../commonJS/dataChanges'
export default {
  name: "traffic",
  mixins: [config],
  components: {

    bdaBarCharts
  },
  props: {
    type: {
      type: String,
      default: () => {
        return '1'
      }
    },
  },
  data() {
    return {
      types: '1',  //当前供电类型 ， 1：主城（渝西），0：渝东南（北）
      gdTitle: '主城渝西',
      gdCount: 0,
      rightDataMap: {
        '抢修类业务量': 0,
        '非抢修类业务量': 0
      },
      barCharts: {},
      barType: 'stack',
      barChartsRes: false,
      barChartsX: [],
      zwBorderRes: true,
    }
  },
  watch: {
    type(value) {
      this.types = value
      this.GetSumTarget()
      this.getGdData()
      if (value === '1') {
        this.gdTitle = '主城渝西'
      } else {
        this.gdTitle = '渝东南北'
      }
    }
  },
  created() {

  },
  mounted() {
    this.GetSumTarget()
    this.getGdData()
  },

  methods: {
    //业务量数据
    GetSumTarget() {
      let $this = this;
      this.getSumTarget(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: this.types,
      }
      )).then(res => {
        if (res && res.length !== 0) {
          for (let item of res) {
            if (item.isrepair === '1') {
              $this.rightDataMap['抢修类业务量'] = item.sum
            } else if (item.isrepair === '0') {
              $this.rightDataMap['非抢修类业务量'] = item.sum
            }
          }
        }
        $this.gdCount = $this.rightDataMap['抢修类业务量'] + $this.rightDataMap['非抢修类业务量']
      });
    },
    // 供电数据
    getGdData() {
      let $this = this;
      $this.barChartsRes = false;
      $this.barCharts = {};
      this.barChartsX = [];
      // this.gdCount = 0;
      this.getTotalVolumeForOrgTyp(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams,
        orgType: this.types
      })).then(res => {
        if (res && res.length !== 0) {
          let data = translateFilter(res, $this.$parent.orgTranslate, 'orgno', 'orgName');
          let _info = dataChange(data, 'orgno', 'orgName');
          let colorMap = {
            '非抢修类': ['#6AFFA2', '#23FFD6'],
            '抢修类': ['#00FFEF', '#007EFF'],
          }

          let nData = dataFilter(_info, {
            name: 'isrepais',
            translate: {
              '0': '非抢修类',
              '1': '抢修类'
            },
            valueField: 'sum'
          }, 'orgName');
          nData.colorMap = colorMap;
          // nData.dimension.splice(nData.dimension.indexOf('江津区'), 1, '江津')
          $this.barCharts = nData;

          $this.barChartsRes = false;
          $this.$nextTick(() => {
            $this.barChartsRes = true;
          })

        }
      }).catch(() => {
        $this.barChartsRes = true;
      })
    },


  }
}
</script>

<style lang="scss" scoped>
.traffic {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  color: #fff;
  .noData {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: #fff;
      font-size: 14px;
    }
  }

  .plant_traffic {
    width: 100%;
    height: 100%;

    position: relative;

    .title {
      color: #fff;
      line-height: 40px;
      text-align: center;
      font-size: 22px;
      letter-spacing: 2px;
    }

    .plant_traffic-main {
      width: 100%;
      height: 100%;
      position: relative;
      display: flex;
      .plant_traffic-left {
        width: 80%;
        box-sizing: border-box;
        padding: 20px 40px 0 40px;
        height: calc(100% - 10px);
        float: left;
        height: 100%;
        background: url("../../../assets/img/监控框3.png") no-repeat;
        background-size: 100% 100%;
      }

      .plant_traffic-right {
        flex: 1;
        width: 20%;
        float: left;
        height: 100%;
        position: relative;

        .plant_traffic-right-main {
          height: 100%;
          position: relative;

          .details-style {
            padding: 0px 30px;
            // margin-top: 2px;
            box-sizing: border-box;
            height: 32.5%;
            width: 100%;
            display: flex;
            position: relative;
            background-size: 100% 100%;
            background: url("../../../assets/img/业务量框.png") no-repeat center;

            .title-info {
              width: 60%;
              font-size: 18px;
              display: flex;
              // justify-content: center;
              align-items: center;
              text-align: left;
            }

            .count-info {
              //  text-align: center;
              //   line-height: 35px;
              color: #00e7ff;
              font-size: 22px;
              font-weight: 600;
              width: 40%;
              display: flex;
              // justify-content: center;
              justify-content: flex-end;
              align-items: center;
              text-align: right;
            }
            &:nth-child(2) {
              .count-info {
                color: #00ffb2;
              }
            }
            &:nth-child(3) {
              .count-info {
                color: #86ff80;
              }
            }
          }
        }
      }
    }
  }
}
</style>